// chart
main {
    width: calc(100% - 60px);
    padding: 50px;
    @media (max-width: 767px) {
        padding: 30px 15px;
    }
    section {
        background-color: $gray;
        transition: 300ms all linear;
        @media (max-width: 767px) {
            padding: 20px;
        }
        .chart-section {
            .items {
                margin-bottom: 30px;
                .chart-2 {
                    overflow: auto;
                }
                .border-chart {
                    padding: 30px;
                    border: 1px solid $black;
                }
            }
        }
    }
}

// custom hamburger menu start
span.toggle {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    span {
        width: 25px;
        height: 2px;
        background-color: $white;
        display: inline-block;
        margin-bottom: 5px;
        transition: all .5s ease 0s;
    }
}

.icon+span {
    white-space: pre;
    display: none;
}

// custom hamburger menu end
// main section 
.main-content {
    display: flex;
    top: 58px;
    position: relative;
    .sidebar {
        left: 0;
        width: 60px;
        bottom: 0;
        background-color: $base-color;
        transition: 300ms all linear;
        z-index: 1;
        top: 0;
        position: relative;
        ul {
            display: flex;
            flex-direction: column;
            li {
                text-align: center;
                a {
                    display: flex;
                    color: $white;
                    align-items: center;
                    min-height: 22px;
                    padding: 10px;
                    text-decoration: none;
                    justify-content: center;
                    transition: all .5s ease 0s;
                    &:hover {
                        background-color: $black;
                    }
                    i {
                        font-size: 25px;
                    }
                }
            }
        }
    }
}

// click events
.side-expanded,
.side-hovered {
    .sidebar {
        width: 250px;
        transition: 300ms all linear;
        ul {
            li {
                a {
                    justify-content: flex-start;
                    padding-left: 20px;
                }
            }
        }
    }
    main {
        width: calc(100% - 250px);
    }
    .icon+span {
        display: block;
    }
    span.icon {
        margin: 0 15px 0 0;
    }
    .toggle {
        span:nth-child(2) {
            opacity: 0;
        }
        span:nth-child(1) {
            -webkit-transform: translateY(7px) rotate(45deg);
            -ms-transform: translateY(7px) rotate(45deg);
            -o-transform: translateY(7px) rotate(45deg);
            transform: translateY(7px) rotate(45deg);
        }
        span:nth-child(3) {
            -webkit-transform: translateY(-7px) rotate(-45deg);
            -ms-transform: translateY(-7px) rotate(-45deg);
            -o-transform: translateY(-7px) rotate(-45deg);
            transform: translateY(-7px) rotate(-45deg);
        }
    }
}

//topbar 
.topbar {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    background-color: $base-color;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 2;
    height: 58px;
    ul {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 0;
        a {
            padding: 10px 15px;
            display: inline-block;
            color: $white;
        }
    }
}

//Progressbar Status
.progressbar-status {
    .progress-row {
        margin-top: 20px;
        margin-left: 0;
        margin-right: 0;
        padding: 30px;
        border: 1px solid $base-color;
        .progress {
            width: $size;
            height: $size;
            line-height: $size;
            background: none;
            margin: 0 auto;
            box-shadow: none;
            position: relative;
            &:after {
                content: "";
                width: 100%;
                height: 100%;
                border-radius: 50%;
                border: $borderWidth solid $border-color-default;
                position: absolute;
                top: 0;
                left: 0;
            }
            >span {
                width: 50%;
                height: 100%;
                overflow: hidden;
                position: absolute;
                top: 0;
                z-index: 1;
            }
            .progress-left {
                left: 0;
            }
            .progress-bar {
                width: 100%;
                height: 100%;
                background: none;
                border-width: $borderWidth;
                border-style: solid;
                position: absolute;
                top: 0;
                border-color: $border-color-fill;
            }
            .progress-left .progress-bar {
                left: 100%;
                border-top-right-radius: ($size/2);
                ;
                border-bottom-right-radius: ($size/2);
                ;
                border-left: 0;
                -webkit-transform-origin: center left;
                transform-origin: center left;
                //animation: loading-2 1.5s linear forwards 1.8s;
            }
            .progress-right {
                right: 0;
                .progress-bar {
                    left: -100%;
                    border-top-left-radius: ($size/2);
                    ;
                    border-bottom-left-radius: ($size/2);
                    ;
                    border-right: 0;
                    -webkit-transform-origin: center right;
                    transform-origin: center right;
                    //animation: loading-1 1.8s linear forwards;
                }
            }
            .progress-value {
                display: flex;
                border-radius: 50%;
                font-size: 1rem;
                text-align: center;
                line-height: 20px;
                align-items: center;
                justify-content: center;
                height: 100%;
                width: 100%;
                font-weight: 300;
                span {
                    font-size: 12px;
                    text-transform: uppercase;
                }
            }
        }
        .status-bar {
            text-align: center;
            font-size: 14px;
            color: $base-color;
            font-weight: bold;
            margin-top: 10px;
            h5 {
                font-size: inherit;
            }
        }
    }
}

// Data tabel
.table {
    margin-top: 30px;
    th {
        background-color: $base-color;
        color: $white;
    }
}

// data-table-wrapper
#two {
    margin-top: 30px;
    .dt-buttons,
    .dataTables_length,
    .dataTables_info {
        margin-bottom: 15px;
        @media (max-width: 767.5px) {
            margin-right: auto;
        }
    }
    .dataTables_info {
        @media (max-width: 767.5px) {
            display: block;
            width: 100%;
        }
    }
    .dt-buttons {
        input {
            margin-left: 20px;
        }
    }
}

//data table
.dataTables_wrapper {
    overflow-x: auto;
    overflow-y: auto;
    max-width: 100%;
    // display: inline-block;
    @media (max-width: 767.5px) {
        flex-direction: column;
    }
    width: 100%;
    .dataTables_length {
        margin: 0 30px;
        @media (max-width: 767.5px) {
            display: block;
            margin: 0;
            label {
                justify-content: flex-start;
            }
        }
        .form-control {
            margin: 0 10px;
        }
    }
    .dataTables_paginate {
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: 30px;
        .pagination {
            text-align: center;
            .paginate_button {
                background-color: $base-color;
                margin-right: 20px;
                color: $white;
                opacity: .7;
                a {
                    color: $white;
                    padding: 10px;
                    display: inline-block;
                    min-width: 40px;
                    text-align: center;
                    text-decoration: none;
                }
                &.active {
                    opacity: 1;
                }
            }
            .previous,
            .next {
                opacity: 1;
            }
        }
    }
}

//margin for data table
.data-table-only {
    margin: 30px 0;
}

// section section
#two {
    display: none;
}

// chart show and hide
.show-chart {
    #two {
        display: block;
    }
    #one {
        display: none;
    }
}